<script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import crmproduct from "../../../components/Common/Modal/crmproduct.vue";
import crmcall from "../../../components/Common/Modal/crmcall.vue";
import modalproduct from "../../../components/Common/Modal/modalproduct.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modelquoteemail from "../../../components/Common/Modal/modelquoteemail.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";
import quotepreview from "../../../components/Common/InvoicePreview.vue";
export default {
    components: {
    crmtimeline,
    crmnote,
    crmattachment,
    crmproduct,
    crmcall,
    modalattachment,
    modaltask,
    modalevent,
    modalcall,
    modalproduct,
    modelquoteemail,
    crmreadmore,
    quotepreview
  },
    data(){
        return {
            isNotes: false,
            isAttachment: false,
            isTask: false,
            isTimeline: false,
            isContact: false,
            isContactgroup: false,
            isemailLog: false,
            isSchedule: false,
            iscontactDetails: null,
            showContactModal: false,
            showProductModal: false,
            showAddTaskModal: false,
            showAddEventModal: false,
            showCallModal: false,
            sendEmailModal: false,
            showAttachmentModal: false,
            LeftViewData: [],
            noteData: [],
            RightViewData: null,
            attachmentData: [],
            activitiesData: null,
            timelinedata: [],
            productData: null,
            productHeaders: null,
            contactsData: null,
            contactsHeader: null,
            noRecordForTab: false,
            isShowNoteDialogue: false,
            leadsHeader: null,
            dealsHeader: null,
            dealsData: null,
            leadsData: null,
            EmailData: [],
            EmailLogData: [],
            template1: false,
            template2: false,
            template3: false,
            template4: false,
            SummaryData: [],
            orderNumber: '',
            createdAt: '',
            companyName: '',
            clientName: '',
            billingContactPerson: '',
            shippingContactPerson: '',
            billingFullAddress: '',
            billingAccountAddress: '',
            shippingFullAddress: '',
            shippingAccountAddress: '',
            billingCountryData: '',
            billingCityData:'',
            shippingCountryData: '',
            shippingCityData:'',
            billingStateData: '',
            shippingStateData: '',
            billingCountyData: '',
            shippingCountyData: '',
            billingZipCodeData: '',
            shippingZipCodeData: '',
            billingEmailData: '',
            shippingEmailData: '',
            billingUserPhoneData: '',
            shippingUserPhoneData: '',
            address: '',
            email: '',
            phoneNo: '',
            orderTitle: '',
            description: '',
            termAndCondition: '',
            subtotal: '',
            shippingCharges: '',
            adjustments: '',
            netTotal: '',
            callData: null,
            CRMQuotation: false,
            modalItem: null,
            quotationNumber: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
            modalTaskItem: null,
            modalFollowUpItem: null,
            modalCallItem: null,
            modalDeleteItem: null,
            isView: false,
            modal: null,
            stausName: '',
            currentTab: "",
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            EmailListingApiURL: `id=0&refId=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`,
            activeKey: 1, 
            isShowNotePopup: false,
            userid: null,
            companyId: null,
            containername: null,
            editUrl: `/CRM/ManageQuotation/${this.$route.params.id}/${this.$route.params.moduleName}/${this.$route.params.subModuleCode}/0`,
            PreviewData:null,
            isLoading:false
    }
        },       
        created: function () {
           this.userid= this.GetUserInfo.ID;
           this.companyId= this.GetUserInfo.CompanyId;
          this.containername= this.GetUserInfo.ContainerName;
          this.FetchData();
        },
        updated() {
            var vm = this;
            setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);              
        },
        methods: {

            DisableButton: function () {
                var vm = this;
                vm.isNotes = false;
                vm.isAttachment = false;
                vm.isTask = false;
                vm.isTimeline = false;
                vm.isContact = false;
                vm.isemailLog = false;
                vm.isContactgroup = false;
                vm.isSchedule = false;
            },           
            FetchData: function () {
                var vm = this;
                vm.isLoading=true;
                LeadDataService.FormLayoutView(this.ApiURL).then(function (response) {     
                    vm.isLoading=false;        
                    var fullLeftJSON = response.data.LEFTDATA;
                              var stausName = $.grep(fullLeftJSON, function (element, index) {
                                  return element.name == 'status_id';
                              });
                              if (stausName != null && stausName.length > 0) {
                                  vm.stausName = stausName[0];
                              }
                              fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                                  return element.form_field_visibility == 'YES';
                              });
                              if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                                  var fullJSON = response.data.RIGHTDATA;
                                  var gruopBy = "";
                                  $.each(fullJSON, $.proxy(function (index, item) {
                                      gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                                      var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                          return element.groupKey == gruopBy;
                                      });
                                      if (chkGroup.length == 0) {
                                          var returnedData = $.grep(fullJSON, function (element, index) {
                                              return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                          });
                                          if (returnedData.length > 0) {
                                              var objGroup = {
                                                  groupKey: gruopBy,
                                                  groupData: returnedData
                                              };
                                              vm.timelinedata.push(objGroup);
                                          }
                                      }
                                  }, vm));
                                  
                                  vm.noRecordForTab = false;
                              } else {
                                  vm.noRecordForTab = true;
                              }
                              if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                                  var groupId = fullLeftJSON[0].group_id;
                                  $.each(fullLeftJSON, $.proxy(function (index, item) {
                                      if (item.name == 'order_number') {
                                          vm.quotationNumber = item.value;
                                      }
                                      if (item.name == 'quotation_type') {
                                          if (item.value == '2' || item.value == 2) {
                                              item.value = 'Deal';
                                          }
                                          else if (item.value == '1' || item.value == 1) {
                                              item.value = 'Project';
                                          }
                                          if (item.value == '3' || item.value == 3) {
                                              item.value = 'General';
                                          }
                                      }
                                      var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                                          return element.group_id == groupId;
                                      });
                                      if (chkGroup.length == 0) {
                                          var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                              return element.group_id == groupId;
                                          });
                                          if (returnedData.length > 0) {
                                              var objGroup = {
                                                  group_id: groupId,
                                                  group_name: returnedData[0].group_name,
                                                  LeftViewData: returnedData
                                              };
                                              vm.LeftViewData.push(objGroup);
                                          }
                                      }
                                      groupId = item.group_id;
                                  }, vm));
                              }
                            
                              setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                              setTimeout(function () {vm.SetResponsiveTab()}, 200);
                              setTimeout(function () {vm.RadioButtonBootstrap()}, 500);
                          })
            },
            openShowNotePopup: function () {
      var ths = this;
      if (
        typeof ths.checkForAdditionalPermission !== "undefined" &&
        !ths.checkForAdditionalPermission("AddNote")
      ) {
        ths.ShowAlert(
          ths.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          ths.$t("Alert")
        );
      } else {
        ths.isShowNotePopup = true;
        $("#tab-notes").trigger("click");
        ths.loadData("notes");
      }
    },
            openNoteDialogue() {
                this.isShowNoteDialogue = (this.isShowNoteDialogue) ? false : true;
            },
            openEmailModal(item) {
                this.sendEmailModal = true;
                this.modalItem = item;
            },
           
            openAddTaskModel() {
                this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
            },
            openAddNewEvent() {
                this.showAddEventModal = (this.showAddEventModal) ? false : true;
            },
            openCallEvent() {
                this.showCallModal = (this.showCallModal) ? false : true;
            },
            openActivityModal(item) {
                if (item.activity_type == "Task") {
                    this.showAddTaskModal = true;
                    this.modalTaskItem = item;
                }
                else if (item.activity_type == "FollowUp") {
                    this.showAddEventModal = true;
                    this.modalFollowUpItem = item;
                }
                else if (item.activity_type == "Call") {
                    this.showCallModal = true;
                    this.modalCallItem = item;
                }
            },
            DeleteActivity(item) {
                this.modalDeleteItem = item;
                this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
            },
            openContactModal(contactDetails) {
                this.iscontactDetails = contactDetails;
                this.showContactModal = (this.showContactModal) ? false : true;
            },
            DeleteActivityData: function (id) {               
                var vm = this;
                if (id > 0) {
                    vm.confirmR(vm.$t("ConfirmDelete"),true,null, function (result) {
                            var postJSON = {
                                ids: id,
                                moduleName: "CRM",
                                subModuleCode: vm.$route.params.subModuleCode,
                                refCode: 'DELETE_VIEW_ACTIVITY',
                                refId: vm.$route.params.id,
                            };
                            var objectPostString = JSON.stringify(postJSON);                           
                            LeadDataService.CommonDelete(objectPostString).then(function (response) {
                            if (response.data[0].Status == "Success") {
                                    vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Activity')]), "success", true, vm.$t("Alert"));  
                                    vm.loadData('activities');
                                }
                                else {                                    
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Activity')]), "warning", true, vm.$t("Alert"));  
                                }
                                 
                            });                        
                    });
                }
            },
            deleteContactFromTab(value) {
                var vm = this;
                if (value > 0) {
                    vm.confirmR(vm.$t("ConfirmDelete"),true,null, function (result) {
                        var postJSON = {
                        ids: value,
                        moduleName: "CRM",
                        subModuleCode: vm.$route.params.subModuleCode,
                        refCode: "DELETE_VIEW_CONTACTS",
                        refId: vm.$route.params.id,
                    };                           
                            var objectPostString = JSON.stringify(postJSON);
                            LeadDataService.CommonDelete(objectPostString)
                            .then(function(response) {
                                var box = "";
                                if (response.data[0].Status == "Success") {
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    vm.loadData('contacts');
                                    box =vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Contact')]), "success", true, vm.$t("Alert"));
                                }
                                else {
                                    box =vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Contact')]), "warning", true, vm.$t("Alert")); 
                                }
                                setTimeout(function () {
                                    box.modal('hide');
                                }, 2000);
                                 
                            });                                                  
                    });
                }
                else {
                    vm.ShowAlert(vm.$t("DeletedError",[vm.$t("Contact")]),'failure',true,vm.$t('Alert'));
                }
            },            
            loadData: function (tabName,callFrom) {
                var vm = this;
                vm.currentTab = tabName;
                vm.DisableButton();
                if (tabName == "notes") {
                    vm.isNotes = true;
                    vm.activeKey = 2;
        if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
                }
                else if (tabName == "attachments") {
                    vm.isAttachment = true;
                    vm.activeKey = 3;
                }
                else if (tabName == "activities") {
                    vm.isTask = true;
                    vm.isTimeline = true;
                    vm.activeKey = 4;
                }
                else if (tabName == "emailLog") {
                    vm.isemailLog = true;
                    vm.activeKey = 6;
                }
                if (tabName != "" && tabName != null) {
                     
                    var url ='';
                    if (tabName == "attachments") {
                        LeadDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                             
                            if (response.data != null) {
                                vm.attachmentData = response.data.data;
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "notes") {
                        vm.noteData = [];
                        LeadDataService.NoteListing(vm.ApiURL).then(function (response) {
                             
                            if (response.data != null && response.data.data.length > 0) {
                                vm.noteData = response.data.data;
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "activities") {
                        LeadDataService.ActivityListing(vm.ApiURL).then(function (response) {
                             
                            if (response.data != null && response.data.data.length > 0) {
                                vm.activitiesData = response.data.data;
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                setTimeout(function () {vm.ResponsiveDataTable('table-openactivities-content')}, 500);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "emailLog") {                      
                        url = vm.EmailListingApiURL + "&sortBy=" + this.SortBy + "&sortExp=" + this.SortExp + "&pageSize=" + this.PageSize + "&pageNumber=" + this.PageNumber;
                        LeadDataService.EmailListing(url).then(function (response) {                             
                            if (response.data != null && response.data != "" && response.data.data.length > 0) {
                                vm.EmailLogData = response.data.data;
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                setTimeout(function () {vm.ResponsiveDataTable('table-emails-log-content')}, 500);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                      
                    }
                    else if (tabName == "products") {
                        url = vm.ApiURL + "&sortBy=" + this.SortBy + "&sortExp=" + this.SortExp + "&pageSize=" + this.PageSize + "&pageNumber=" + this.PageNumber;
                        LeadDataService.getItemListForAssociate(url).then(function (response) {
                             
                            if (response.data != null) {
                                vm.productData = response.data.data;
                                vm.productHeaders = response.data.schema;
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                                vm.productData = null;
                            }
                        });
                        vm.activeKey = 5;
                    }
                    else if (tabName == "calls") {
                        url =`id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}`;
                        LeadDataService.GetCallLogsListinge(url).then(function (response) {
                             
                            if (response.data != null) {
                                vm.callData = response.data.data;
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                                vm.calltData = null;
                            }
                        });
                        vm.activeKey = 8;
                    }
                    else if (tabName == "timeline") {
                        url =`id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}`;
                        LeadDataService.TimelineListing(url).then(function (response) {
                             
                            if (response.data != null) {
                                vm.timelinedata = [];
                                var fullJSON = response.data.data;
                                var gruopBy = "";
                                $.each(fullJSON, $.proxy(function (index, item) {
                                    gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                                    var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                        return element.groupKey == gruopBy;
                                    });
                                    if (chkGroup.length == 0) {
                                        var returnedData = $.grep(fullJSON, function (element, index) {
                                            return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                        });
                                        if (returnedData.length > 0) {
                                            var objGroup = {
                                                groupKey: gruopBy,
                                                groupData: returnedData
                                            };
                                            vm.timelinedata.push(objGroup);
                                        }
                                    }
                                }, vm));
                                setTimeout(function () {vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                        vm.activeKey = 1;
                    }
                    else if (tabName == "email") {
                      
                        url =`id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&type=QUOTE_TEMPLATE`;
                         
                        LeadDataService.getDDLData(url).then(function (response) {
                              var json = response.data;
                               
                              if (json != null) {
                                  vm.TemplateName = json.DATA[0].name;
                                  if (vm.TemplateName == "Bill1") {
                                      vm.template1 = true;
                                      vm.template2 = false;
                                      vm.template3 = false;
                                      vm.template4 = false;
                                  }
                                  else if (vm.TemplateName == "Bill2") {
                                      vm.template1 = false;
                                      vm.template2 = true;
                                      vm.template3 = false;
                                      vm.template4 = false;
                                  }
                                  else if (vm.TemplateName == "Bill3") {
                                      vm.template1 = false;
                                      vm.template2 = false;
                                      vm.template3 = true;
                                      vm.template4 = false;
                                  }
                                  else if (vm.TemplateName == "Bill4") {
                                      vm.template1 = false;
                                      vm.template2 = false;
                                      vm.template3 = false;
                                      vm.template4 = true;
                                  }
                                   
                                  if (vm.TemplateName != null) {
                                    var url =`id=${vm.$route.params.id}&moduleCode=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}`;
                                    LeadDataService.QuotationDetails(url).then(function (response) {
                                            var addressData = response.data.contact_person;
                                            vm.PreviewData = response.data;
                                            if (response.data != null) {
                                                //vm.quotationData = data.addressData[0];
                                                var productSummaryData = [];
                                                var i = 1;
                                                $.each(response.data.dataproductlineitemjson, function (key, item) {
                                                    var prductName = "";
                                                    var prductId = 0;
                                                    if (item.PRODUCT_NAME != null) {
                                                        try {
                                                            item.PRODUCT_NAME = JSON.parse(item.PRODUCT_NAME);
                                                        }
                                                        catch (ex) { }
                                                        if (Array.isArray(item.PRODUCT_NAME)) {
                                                            prductName = item.PRODUCT_NAME[0].name;
                                                            prductId = item.PRODUCT_NAME[0];
                                                        }
                                                        else {
                                                            prductName = item.PRODUCT_NAME;
                                                        }
                                                    } else {
                                                        prductName = item.ITEM_NAME;
                                                    }
                                                    //if (projectId > 0) {
                                                    var dataObject = {
                                                        SNo: i,
                                                        ProductId: prductId,
                                                        ProductName: prductName,
                                                        ProductList: item.ITEM_PRODUCT_LIST_PRICE,
                                                        ProductQuantity: item.ITEM_PRODUCT_QUANTITY,
                                                        ProductAmount: item.ITEM_PRODUCT_AMOUNT,
                                                        ProductDiscount: item.ITEM_PRODUCT_DISCOUNT,
                                                        ProductDesc: item.ITEM_PRODUCT_DESC,
                                                        ProductTax: JSON.parse(item.TAX_DATA) == null ? "0" : JSON.parse(item.TAX_DATA)[0].TAX_RATE,
                                                        ProductTotal: item.ITEM_TOTAL,
                                                    };
                                                    productSummaryData.push(dataObject);
                                                    i++;
                                                    //}
                                                });
                                                if (productSummaryData.length > 0) {
                                                    vm.SummaryData = productSummaryData;
                                                }
                                                else {
                                                    vm.SummaryData = "";
                                                }
                                                if (addressData[0] !== undefined && addressData[0] != null) {
                                                    vm.orderNumber = addressData[0].ORDER_NUMBER == null ? '' : addressData[0].ORDER_NUMBER;
                                                    vm.createdAt = addressData[0].CREATED_AT == null ? '' : addressData[0].CREATED_AT;
                                                    vm.companyName = addressData[0].COMPANY_NAME == null ? '' : addressData[0].COMPANY_NAME;
                                                    vm.address = addressData[0].ADDRESS == null ? '' : addressData[0].ADDRESS;
                                                    vm.email = addressData[0].EMAIL == null ? '' : addressData[0].EMAIL;
                                                    vm.phoneNo = addressData[0].PHONE_NUMBER == null ? '' : addressData[0].PHONE_NUMBER;
                                                    vm.clientName = addressData[0].ACCOUNT_NAME == null ? '' : addressData[0].ACCOUNT_NAME;
                                                    vm.billingContactPerson = addressData[0].CONTACT_PERSON_NAME == null ? '' : addressData[0].CONTACT_PERSON_NAME;
                                                    vm.billingAccountAddress = (addressData[0].ACCOUNT_ADDRESS == null || addressData[0].ACCOUNT_ADDRESS == '') ? '' : addressData[0].ACCOUNT_ADDRESS + ',';
                                                    vm.billingCountryData = (addressData[0].COUNTRY_NAME == null || addressData[0].COUNTRY_NAME == '') ? '' : addressData[0].COUNTRY_NAME + ',';
                                                    vm.billingCityData = (addressData[0].CITY_NAME == null || addressData[0].CITY_NAME == '') ? '' : addressData[0].CITY_NAME + ',';
                                                    vm.billingStateData = (addressData[0].STATE_NAME == null || addressData[0].STATE_NAME == '') ? '' : addressData[0].STATE_NAME + ',';
                                                    vm.billingCountyData = (addressData[0].COUNTY_NAME == null || addressData[0].COUNTY_NAME == '') ? '' : addressData[0].COUNTY_NAME + ',';
                                                    vm.billingZipCodeData = (addressData[0].ZIP_CODE_NAME == null || addressData[0].ZIP_CODE_NAME == '') ? '' : addressData[0].ZIP_CODE_NAME + ',';
                                                    vm.billingEmailData = addressData[0].EMAIL_ID == null ? '' : addressData[0].EMAIL_ID;
                                                    vm.billingUserPhoneData = addressData[0].MOBILE_PHONE == null ? '' : addressData[0].MOBILE_PHONE;
                                                    vm.orderTitle = addressData[0].ORDER_TITLE == null ? '' : addressData[0].ORDER_TITLE;
                                                    vm.description = addressData[0].DESCRIPTION == null ? '' : addressData[0].DESCRIPTION;
                                                    vm.termAndCondition = addressData[0].TERM_AND_CONDITIONS == null ? '' : addressData[0].TERM_AND_CONDITIONS;
                                                    vm.billingFullAddress = vm.billingAccountAddress + vm.billingCountryData + vm.billingCityData + vm.billingZipCodeData;
                                                    vm.billingFullAddress = vm.removeLastChar(vm.billingFullAddress, ',');
                                                }
                                                if (addressData[1] !== undefined && addressData[1] != null) {
                                                    vm.shippingContactPerson = addressData[1].CONTACT_PERSON_NAME == null ? '' : addressData[1].CONTACT_PERSON_NAME;
                                                    vm.clientName = addressData[1].ACCOUNT_NAME == null ? '' : addressData[1].ACCOUNT_NAME;
                                                    vm.shippingAccountAddress = (addressData[1].ACCOUNT_ADDRESS == null || addressData[1].ACCOUNT_ADDRESS == '') ? '' : addressData[1].ACCOUNT_ADDRESS + ',';
                                                    vm.shippingCountryData = (addressData[1].COUNTRY_NAME == null || addressData[1].COUNTRY_NAME == '') ? '' : addressData[1].COUNTRY_NAME + ',';
                                                    vm.shippingCityData = (addressData[1].CITY_NAME == null || addressData[1].CITY_NAME == '') ? '' : addressData[1].CITY_NAME + ',';
                                                    vm.shippingStateData = (addressData[1].STATE_NAME == null || addressData[1].STATE_NAME == '') ? '' : addressData[1].STATE_NAME + ',';
                                                    vm.shippingCountyData = (addressData[1].COUNTY_NAME == null || addressData[1].COUNTY_NAME == '') ? '' : addressData[1].COUNTY_NAME + ',';
                                                    vm.shippingZipCodeData = (addressData[1].ZIP_CODE_NAME == null || addressData[1].ZIP_CODE_NAME == '') ? '' : addressData[1].ZIP_CODE_NAME + ',';
                                                    vm.shippingEmailData = addressData[1].EMAIL_ID == null ? '' : addressData[1].EMAIL_ID;
                                                    vm.shippingUserPhoneData = addressData[1].MOBILE_PHONE == null ? '' : addressData[1].MOBILE_PHONE;
                                                    vm.shippingFullAddress = vm.shippingAccountAddress + vm.shippingCountryData + vm.shippingCityData + vm.shippingZipCodeData;
                                                    vm.shippingFullAddress = vm.removeLastChar(vm.shippingFullAddress, ',');
                                                }
                                                var amount = response.data.dataproductlineitemjsonamount;
                                                var jsonAmount = response.data.dataproductlineitemjsonamount[0];
                                                if (amount !== undefined && amount != null && jsonAmount !== undefined && jsonAmount != null) {
                                                    vm.subtotal = jsonAmount.summary_sub_total == null ? '' : jsonAmount.summary_sub_total;
                                                    vm.shippingCharges = jsonAmount.summary_shipping_charges == null ? '' : jsonAmount.summary_shipping_charges;
                                                    vm.adjustments = jsonAmount.summary_adjustments == null ? '' : jsonAmount.summary_adjustments;
                                                    vm.netTotal = jsonAmount.summary_total == null ? '' : jsonAmount.summary_total;
                                                }
                                                vm.CRMQuotation = true;
                                                vm.noRecordForTab = false;
                                            }
                                            else {
                                                vm.CRMQuotation = false;
                                                vm.noRecordForTab = true;
                                            }
                                             
                                        });
                                        vm.activeKey = 7;
                                  }
                              }
                              else {
                                   
                                  this.noRecordForTab = true;
                              }
                          });
                          
                    }
                }
            },
            
        }
}
</script>
